<template>
  <div class="idetails">
    <!-- 对佣详情 -->
    <template v-if="!suCinfoOff&&!upload">
      <!--  对佣详情 -->
      <el-card class="title">
        <img src="../../../assets/icons/left.png" />
        <span @click="$emit('InvoiceOffFN')">返回收佣列表</span>
        <b>对佣详情</b>
        <i style="cursor: pointer;" v-if="ideaTop.status==1" @click="DeletAll()" class="top_icon el-icon-delete"></i>
      </el-card>
      <!-- context内容 -->
      <div :dataFn="dataFn" class="scoll_context">
        <el-card style="box-sizing:border-box;" class="box-card herders">
          <div slot="header" class="clearfix">
            <span>{{ideaTop.address}}&nbsp;&nbsp;{{ideaTop.checkDate}}</span>
          </div>
          <el-row>
            <el-col :span="8">
              <div style="min-width:100px;text-align: right;">结佣类型:</div>
              <strong class="rights_text">{{ideaTop.typeName}}</strong>
            </el-col>
            <el-col :span="8">
              <div style="min-width:100px;text-align: right;">门店:</div>
              <strong class="rights_text">{{ideaTop.deptName}}</strong>
            </el-col>
            <el-col :span="8">
              <div style="min-width:100px;text-align: right;">账户:</div>
              <strong class="rights_text">{{ideaTop.accName}}</strong>
              <img
                style="margin-left:10px;min-width:20px; cursor: pointer;"
                @click="AccountOff.trunoff=true"
                src="../../../assets/icons/alert.png"
              />
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <div style="min-width:100px;text-align:right;">结佣金额:</div>
              <strong class="rights_text" style="color:red">{{ideaTop.money}}￥</strong>
            </el-col>
            <el-col :span="8">
              <div style="min-width:100px;text-align: right;">实际支付:</div>
              <strong class="rights_text">{{ideaTop.realMoney}}</strong>
            </el-col>
            <el-col :span="8">
              <div style="min-width:100px;text-align: right;">是否开票:</div>
              <el-switch
                class="rights_text"
                :active-color="'#0971cc'"
                :value="ideaTop.isBill==1?true:false"
                disabled
              ></el-switch>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <div style="min-width:100px;text-align: right;">发票类型:</div>
              <strong class="rights_text">{{ideaTop.invoiceType==1?'专票':'普票'}}</strong>
            </el-col>
            <el-col :span="8">
              <div style="min-width:100px;text-align: right;">发票抬头:</div>
              <strong class="rights_text">{{ideaTop.title}}</strong>
              <img
                style="margin-left:10px;min-width:20px;max-height: 20px; cursor: pointer;"
                @click="infoOff.trunoff=true"
                src="../../../assets/icons/alert.png"
              />
            </el-col>
            <el-col :span="8">
              <div style="min-width:100px;text-align: right;">代扣税点:</div>
              <strong
                class="rights_text"
              >{{ideaTop.scaleId==1?'不扣税': ideaTop.scaleId==2?'扣3%':ideaTop.scaleId==3?'扣6%':ideaTop.scaleId== 4?'扣5%':''}}</strong>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <div style="min-width:100px;text-align: right;">备注:</div>
              <strong class="rights_text">{{ideaTop.addRemark}}</strong>
            </el-col>
          </el-row>
          <el-row>
            <el-col style="display:flex;align-items: center;" :span="24">
              <div style="min-width:100px;text-align: right;">操作日志:</div>
              <operation :status="ideaTop.status" type="0" />
              <el-button
                @click="oJournalFn"
                style="width:106px;heigth:40px;margin-left:40px;color:#637d93"
              >查看更多</el-button>
            </el-col>
          </el-row>
        </el-card>
        <!-- 客户 -->
        <el-card style="margin-top:20px;">
          <header>认购单列表|{{tableData.length}}</header>
          <el-table
            :cell-style="cellStyle"
            :header-cell-style="{background:'#edfaf8',color:'#333333'}"
            :row-class-name="tableRowClassName"
            :data="tableData"
            style="width: 100%"
          >
            <el-table-column prop="customName" fixed label="客户" width="180"></el-table-column>
            <el-table-column prop="midMobile" label="电话号码" width="180">
              <template
                slot-scope="scope"
              >{{scope.row.preMobile+scope.row.midMobile+scope.row.sufMobile }}</template>
            </el-table-column>
            <el-table-column prop="roomNo" label="房号" width="180"></el-table-column>
            <el-table-column prop="tradeTime" label="认购日期" width="180"></el-table-column>
            <el-table-column prop="sumMoney" label="认购金额" width="180">
              <template slot-scope="scope">{{scope.row.sumMoney}}元</template>
            </el-table-column>
            <el-table-column prop="sumComission" label="总佣金" width="180">
              <template slot-scope="scope">{{scope.row.sumComission}}元</template>
            </el-table-column>
            <el-table-column prop="haspay" label="已结佣" width="180">
              <template slot-scope="scope">{{scope.row.haspay}}元</template>
            </el-table-column>
            <el-table-column prop="unpay" label="未结佣" width="180">
              <template slot-scope="scope">{{scope.row.sumMoney}}元</template>
            </el-table-column>
            <el-table-column prop="unbilled" label="未开票" width="180">
              <template slot-scope="scope">{{scope.row.unbilled}}元</template>
            </el-table-column>
            <el-table-column prop="taxPoint" label="代扣税点" width="180">
              <template slot-scope="scope">{{scope.row.taxPoint}}元</template>
            </el-table-column>
            <el-table-column prop="realMoney" label="实际支付" width="180">
              <template slot-scope="scope">{{scope.row.realMoney}}元</template>
            </el-table-column>
            <el-table-column prop="canCommission" label="本次可结" width="180">
              <template slot-scope="scope">{{scope.row.canCommission}}元</template>
            </el-table-column>
            <el-table-column prop="billMoney" label="本次结佣" width="180">
              <template slot-scope="scope">{{scope.row.billMoney}}元</template>
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="100">
              <template slot-scope="scope">
                <el-button @click="handleClick(scope.row)" type="text" size="small">查看成交单</el-button>
              </template>
            </el-table-column>
          </el-table>
          <div>
            <Pagination
              @handleSizeChange="handleSizeChange"
              @handleCurrentChange="handleCurrentChange"
              :paginationData="paginationData"
            />
          </div>
        </el-card>
        <!-- 客户 -->

        <!-- 收票信息 -->
        <el-card style="margin-top:20px;" v-if="ideaTop.status!=3||ideaTop.status!=4">
          <template>
            <div v-if="ideaTop.status==5||ideaTop.status==6||ideaTop.status==7" slot="header">
              <span>收票信息</span>
            </div>
            <el-form :model="ideaTop" ref="ruleForm" label-width="100px" class="demo-ruleForm">
              <el-form-item v-if="ideaTop.status==5||ideaTop.status==6" label="收票凭证:" prop="desc">
                <!-- <el-input type="textarea" :hidden="true" v-model="ruleForm.desc"></el-input> -->
                <template v-if="ideaTop.picUrl">
                  <img
                    style="width:100px;height:80px;margin-right:10px; display:inline-block;"
                    v-for="(item,index) in ideaTop.picUrl"
                    :key="index"
                    :src="item"
                    @click="picviewFn(item)"
                  />
                </template>
              </el-form-item>
              <el-form-item v-if="ideaTop.status==5||ideaTop.status==7" label="备注:">
                <el-input type="textarea" v-model="ideaTop.remark"></el-input>
              </el-form-item>
              <el-form-item v-if="ideaTop.status==6" label="备注:">
                <el-input type="textarea" v-model="ideaTop.doneRemark"></el-input>
              </el-form-item>
            </el-form>
            <!-- 审核 -->
            <div v-if="ideaTop.status==2&&$store.getters.userinfo.role==4" class="miss_down">
              <div @click="examineFn(2)">
                <img src="../../../assets/icons/shenghe.png" />
                审核
              </div>
            </div>
            <!-- 审核 -->
            <!-- 保存 -->
            <div
              v-if="ideaTop.addName===$store.getters.userinfo.realName&&(ideaTop.status==1||ideaTop.status==4||ideaTop.status==7)"
              class="miss_downsave"
            >
              <div>
                <el-button type="danger" @click="addProEidt" icon="el-icon-edit">编辑</el-button>
                <el-button
                  type="primary"
                  v-if="ideaTop.status==1"
                  @click="examineFn(1)"
                  icon="el-icon-check"
                >提交</el-button>
              </div>
            </div>
            <!-- 保存 -->
            <!-- 通知开票 -->
            <div v-if="ideaTop.status==3&&$store.getters.userinfo.role!=8" class="miss_downsave">
              <div>
                <el-button type="danger" @click="goInvoice" icon="el-icon-edit">通知开票</el-button>
              </div>
            </div>
            <!-- 通知开票 -->
            <!-- 确认结佣 -->
            <div v-if="ideaTop.status==5&&$store.getters.userinfo.role==8" class="miss_down">
              <div @click="examineFn(5)">
                <img src="../../../assets/icons/确认收佣icon.png" />
                确认结佣
              </div>
            </div>
            <!-- 确认结佣 -->
            <!-- 财务审核 -->
            <div v-if="ideaTop.status==3&&$store.getters.userinfo.role==8" class="miss_downsave">
              <div>
                <el-button
                  type="danger"
                  @click.native="Refuse"
                  style="background:#f1420ef7;"
                  icon="el-icon-circle-close"
                >拒绝开票</el-button>
                <el-button
                  type="primary"
                  style="background:#0b8d7e;"
                  @click.native="uploadFn"
                  icon="el-icon-edit-outline"
                >去收票</el-button>
              </div>
            </div>
            <!-- 财务审核 -->
          </template>
        </el-card>
        <!-- 收票信息 -->
      </div>
      <!-- context内容 -->
      <!-- 图片预览 -->
      <Picview :dialogVisible="picviewOff" />
      <!-- 图片预览 -->
      <!-- 操作日志弹窗 -->
      <oJournal title="操作日志" :status="ideaTop.status" :dialogVisible="oJournalOff" />
      <!-- 操作日志弹窗 -->
      <!-- 发票抬头弹窗 -->
      <oJournal title="发票抬头" :dialogVisible="infoOff" />
      <!-- 发票抬头弹窗 -->
      <!-- 账户弹窗 -->
      <oJournal title="账户" :dialogVisible="AccountOff" />
      <!-- 账户弹窗 -->
      <!-- 审核弹窗 -->
      <examine type="checkDeptInvoice" :dialogVisible="examineOff" />
      <!-- 审核弹窗 -->
      <!-- 编辑门店 -->
      <editment :dialogVisible="editmentOff" />
      <!-- 编辑门店 -->
      <!-- 拒绝开票 -->
      <okcomssion title="拒绝理由" :dialogVisible="RefuseData" />
      <!-- 拒绝开票 -->
      <!-- 确认结佣 -->
      <sureOk :dialogVisible="sureOff" />
      <!-- 确认结佣 -->
    </template>
    <!-- ////////////////////////////////// -->
    <!-- 成交单详情 -->
    <suCinfo type="mendian" :suCinfoAll="suCinfoAll" @InvoiceOffFN="suCinfoOff=false" v-else-if="suCinfoOff" />
    <!-- 成交单详情 -->
     <!-- 上传收票信息 -->
    <upload :ideaTop="ideaTop" :InvoiceDatas="$store.getters.InvoiceDetail" @InvoiceOffFN="upload=false" v-else-if="upload"/>
    <!-- 上传收票信息 -->
  </div>
</template>
<script>
import suCinfo from "components/common/suCinfo";
import Picview from "components/common/Picview";
import oJournal from "components/common/oJournal";
import editment from "./editment";
import okcomssion from "../commission/okcomssion";
import examine from "../commission/examine";
import upload from './upload'
import sureOk from "./sureOk";
import {
  deptInvoiceLog,
  ticketDeptDetail,
  delDeptInvoice,
  commitDeptInvoice,
  noticeDeptInvoice
} from "@/api/business/management";
import { dateTime } from "@/utils/common.js";
import myMixin from "@/mixin/index";
export default {
  mixins: [myMixin],
  props: {
    InvoiceLogInfo: {
      type: Array
    }
  },
  components: {
    oJournal,
    suCinfo,
    editment,
    examine,
    Picview,
    okcomssion,
    sureOk,
    upload
  },
  data() {
    return {
      title: "金水湾   2019-9-23",
      ideaTop: {
        deptId: 10, //门店ID
        deptName: "创优", //门店名称
        baId: 33, //中介门店银行账号信息表ID
        bankName: "中国建设银行", //开户行
        accName: "创优房地产有限公司", //账号名称
        accountId: "4434XXXX", //银行账户
        scaleId: 1, //待扣税点ID，1：不扣税 2：扣3% 3：扣6% 4：扣5%
        scale: "不扣税", //待扣税点
        type: 1, //结佣类型ID，1：垫佣 2：正常结佣
        typeName: "正常结佣", //结佣类型
        isBill: 1, //是否开票 1：是 0：否
        titleId: 1, //发票抬头ID
        title: "云经纪", //发票抬头
        money: "30000", //结佣金额
        realMoney: "21000", //实际支付金额
        addDate: "2019-08-29", //添加日期
        addUid: "", //添加人UID
        addName: "", //添加人名字
        status: "2", //状态 1:已保存(默认状态)，2 :待审核 3:审核通过  4审核拒绝 5收票 6已结佣 7拒绝结佣
        checkDate: "2019-11-01 08:00:00", //确认日期
        picUrl: ["xxxx", "xxxxx"], //确认收票时的图片数组
        remark: "备注", //确认收票时的备注信息
        invoiceType: 1, //发票类型 1专票 2普票
        addRemark: "CCC", //门店对佣单新增、编辑时的备注
        doneRemark: "ddd", //确认结佣或拒绝结佣的备注
        pdfDlPath:
          "https://zsyunjj.com/admin/yjj/dlDeptInvoice?filePath=xxx.pdf" //pdf文件下载地址
      },
      ruleForm: {
        // desc: this.ideaTop.doneRemark
      },
      rules: {
        desc: [{ required: true, message: "请填写活动形式", trigger: "blur" }]
      },
      tableData: this.$store.getters.InvoiceDetail.details.map(item => {
        item.tradeTime = dateTime(item.tradeTime);
        return item;
      }),
      paginationData: {
        //分页
        pageSize: 10,
        currentPage: 1,
        total: 1000
      },
      oJournalOff: {
        //操作日志弹窗数据
        trunoff: false,
        data: null
      },
      AccountOff: {
        //账户弹窗
        trunoff: false,
        data: {
          bankName: "",
          accName: "",
          accountId: ""
        }
      },
      infoOff: {
        //发票抬头弹窗
        trunoff: false,
        data: {
          kfsName: "",
          dutyNo: "",
          bankName: "",
          mobile: "",
          address: "",
          account: "",
          name: ""
        }
      },
      // 发票部分按钮
      editmentOff: {
        //编辑
        trunoff: false
      },
      examineOff: {
        //审核弹窗
        trunoff: false,
        data: null
      },
      picviewOff: {
        //图片预览
        trunoff: false,
        urlSrc: "" //图片预览
      },
      RefuseData: {
        //拒绝开票
        trunoff: false
      },
      sureOff: {
        //确认结佣
        trunoff: false
      },
      // 发票部分按钮
      // 成交单详情
      suCinfoOff: false,
      suCinfoAll: null,
      ticketId: null,
      // 成交单详情
      upload:false,//收票
    };
  },

  methods: {
    oJournalFn() {
      this.oJournalOff.trunoff = true;
      this.oJournalOff.data = this.InvoiceLogInfo;
    },
    //提交
    examineFn(status) {
      switch (status) {
        case 1:
          this.$confirm("请先核对信息，是否立即提交?", "提示", {
            confirmButtonText: "是",
            cancelButtonText: "否",
            type: "warning"
          })
            .then(() => {
              let invoiceId = this.$store.getters.invoiceId;
              commitDeptInvoice({ invoiceId })
                .then(res => {
                  if (res.data.code === "000") {
                    this.$message.success("提交成功");
                    this.$parent.handleClick()
                  }
                })
                .catch(err => {
                  console.log(err);
                });
            })
            .catch(() => {});
          break;
        case 2:
          this.examineOff.trunoff = true;
          break;
        case 5:
          this.sureOff.trunoff = true;
          break;
      }
    },
    // 编辑
    addProEidt() {
      this.editmentOff.trunoff = true;
    },
    //通知开票
    goInvoice() {
      let invoiceId = this.$store.getters.invoiceId;
      noticeDeptInvoice({ invoiceId }).then(res => {
        if (res.data.code === "000") {
          this.$message.success("通知成功");
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    //成交单详情
    handleClick(val) {
      this.suCinfoOff = true;
      this.ticketId = val.ticketId;
      ticketDeptDetail({ ticketId: this.ticketId }).then(res => {
        if (res.data.code === "000") {
          this.suCinfoAll = res.data.bizData;
        }
      });
    },
    handleSizeChange() {},
    handleCurrentChange() {},
    //删除对佣详情(已保存)
    DeletAll() {
      this.$confirm("删除后不可恢复，是否删除?", "提示", {
        confirmButtonText: "是",
        cancelButtonText: "否",
        type: "warning"
      })
        .then(() => {
          delDeptInvoice({
            invoiceId: this.$store.getters.invoiceId
          }).then(res => {
            this.$message.success("删除成功");
            this.$emit('InvoiceOffFN')
            this.$parent.onSubmit()
          });
        })
        .catch(() => {
          console.log("取消成功");
        });
    },
    //图片预览
    picviewFn(val) {
      this.picviewOff.urlSrc = val;
      this.picviewOff.trunoff = true;
    },
    //拒绝开票
    Refuse() {
      this.RefuseData.trunoff = true;
    },
    //数据操作渲染
    handDataBar() {
      let datas = this.AccountOff.data;
      let details = this.$store.getters.InvoiceDetail;
      let companyList = this.$store.getters.companyList;
      let data = companyList.find(item => {
        return item.companyId === details.titleId;
      });
      this.infoOff.data = data;
      for (let key in this.ideaTop) {
        if (key === "status") {
          this.ideaTop[key] = details[key].toString();
          continue;
        }
        this.ideaTop[key] = details[key];
      }
      for (let key in datas) {
        datas[key] = details[key];
      }
    },
    //去收票
    uploadFn(){
      this.upload=true
    }
  },
  computed:{
      dataFn(){
        this.handDataBar()
        return '1'
      }
  },
  // watch: {
  //   '$store.state.management.InvoiceDetail':{
  //     handler(val,old){
  //         this.handDataBar()
  //     },
  //     deep:true
  //   }
  // },
  mounted() {
    this.handDataBar()
  },
  updated(){
    this.handDataBar()
  }
};
</script>
<style lang="scss">
@import "@/css/common.scss";
.idetails {
  .scoll_context {
    height: calc(100vh - 200px);
    overflow-y: auto;
    margin-top: 20px;
    .rights_text {
      margin-left: 20px;
      font-weight: normal;
    }
    .el-col-8 {
      display: flex;
    }
    .el-col-24 {
      display: flex;
    }
    .miss_down {
      display: flex;
      justify-content: center;
      margin-top: 70px;
      margin-bottom: 50px;

      div {
        font-size: 26px;
        padding: 6px 16px;
        background-color: #0b8d7e;
        width: 140px;
        color: #fff;
        border-radius: 6px;
        text-align: center;
        cursor: pointer;
        img {
          vertical-align: middle;
        }
      }
    }
    .miss_downsave {
      display: flex;
      justify-content: center;
    }
    .el-table .warning-row {
      background-color: #edfaf8;
    }

    .el-table .success-row {
      background: #fafafa;
    }
  }
}
.herders {
  .el-row {
    margin-bottom: 20px;
  }
  width: 100%;
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both;
  }
}
.el-card .box-card .herders .is-always-shadow {
  box-sizing: border-box;
}
</style>