<template>
  <div>
    <!-- 添加门店对佣 -->
    <el-dialog
      title="添加门店对佣"
      :visible.sync="dialogVisible.trunoff"
      width="90%"
      :before-close="handleClose"
      class="addment"
    >
      <!-- 门店信息 -->
      <el-card>
        <div slot="header" class="clearfix">
          <span>门店信息</span>
        </div>
        <el-form
          label-width="100px"
          :label-position="labelPosition"
          :rules="rules"
          :model="formInline"
          class="demo-form-inline"
          ref="elform"
        >
          <el-row>
            <el-col :span="8">
              <!-- <el-form-item  label="审批人">
                <el-input v-model="formInline.user" placeholder="审批人"></el-input>
              </el-form-item>-->
              <el-form-item prop="type" label="结佣类型:">
                <el-select style="width:90%;" v-model="formInline.type" placeholder="请选择结佣类型">
                  <el-option label="垫佣" :value="1"></el-option>
                  <el-option label="正常结佣" :value="2"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="deptId" label="门店:">
                <!-- <el-select style="width:90%;" v-model="formInline.deptId" placeholder="请输入门店名字搜索">
                  <el-option label="区域一" value="shanghai"></el-option>
                  <el-option label="区域二" value="beijing"></el-option>
                </el-select>-->
                <el-autocomplete
                  class="inline-input"
                  v-model="state"
                  style="width:90%;"
                  :fetch-suggestions="querySearch"
                  placeholder="请输入内容"
                  @select="handleSelect"
                ></el-autocomplete>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="baId" label="账户:">
                <el-input
                  style="width:90%;"
                  v-model="name"
                  @focus="baIdFn"
                  @keyup.native="baIdFn"
                  placeholder="请选择账户"
                />
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item prop="isBill" label="是否开票:">
                <el-switch v-model="formInline.isBill"></el-switch>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="scale" label="代扣税点:">
                <el-select style="width:90%;" v-model="formInline.scale" placeholder="代扣税点">
                  <el-option label="不扣税" :value="1"></el-option>
                  <el-option label="扣3%" :value="2"></el-option>
                  <el-option label="扣6%" :value="3"></el-option>
                  <el-option label="扣5%" :value="4"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <template v-if="isBillFn">
              <el-col :span="8">
                <el-form-item prop="invoiceType" label="发票类型:">
                  <el-select
                    style="width:90%;"
                    v-model="formInline.invoiceType"
                    placeholder="请选择发票类型"
                  >
                    <el-option label="专票" :value="1"></el-option>
                    <el-option label="普票" :value="2"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </template>
          </el-row>
          <template v-if="isBillFn">
            <el-row>
              <el-col :span="8">
                <el-form-item prop="title" label="发票抬头:">
                  <el-select style="width:90%;" v-model="formInline.title" placeholder="发票抬头">
                    <el-option label="中山市云经纪网络科技有限公司" :value="1"></el-option>
                    <el-option label="中山市创优房地产顾问有限公司" :value="2"></el-option>
                    <el-option label="中山市优创动力管理策划有限公司" :value="3"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
          </template>
          <el-row>
            <el-col :span="24">
              <el-form-item label="备注:">
                <el-input
                  style="width:100%"
                  type="textarea"
                  placeholder="请添加备注"
                  v-model="formInline.remark"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-card>
      <!-- 门店信息 -->
      <i @click="addFn" class="el-icon-plus addIcon">添加认购单</i>
      <!-- 认购单列表 -->
      <template v-if="tableDataFn">
        <el-table
          ref="multipleTable"
          :data="tableDataFn"
          tooltip-effect="dark"
          style="width: 100%"
          @selection-change="handleSelectionChange"
        >
          <el-table-column fixed prop="customName" label="姓名" width="120"></el-table-column>
          <el-table-column prop="preMobile" width="220" label="电话号码">
            <template
              slot-scope="scope"
            >{{ scope.row.preMobile+scope.row.midMobile+scope.row.sufMobile }}</template>
          </el-table-column>
          <el-table-column prop="projectName" label="楼盘名字"></el-table-column>
          <el-table-column prop="roomNo" label="房号"></el-table-column>
          <el-table-column label="认购日期" width="220">
            <template slot-scope="scope">{{ scope.row.tradeTime }}</template>
          </el-table-column>
          <el-table-column prop="sumMoney" width="120" label="认购金额">
            <template slot-scope="scope">{{ scope.row.sumMoney }}元</template>
          </el-table-column>
          <el-table-column prop="sumComission" width="120" label="总佣金">
            <template slot-scope="scope">{{ scope.row.sumComission }}元</template>
          </el-table-column>
          <el-table-column prop="haspay" width="120" label="已结佣">
            <template slot-scope="scope">{{ scope.row.haspay }}元</template>
          </el-table-column>
          <el-table-column prop="unpay" width="120" label="未结佣">
            <template slot-scope="scope">{{ scope.row.unpay }}元</template>
          </el-table-column>
          <el-table-column prop="unbilled" width="120" label="未开票">
            <template slot-scope="scope">{{ scope.row.unbilled }}元</template>
          </el-table-column>
          <el-table-column prop="canCommission" width="120" label="本次可结">
            <template slot-scope="scope">{{ scope.row.canCommission }}元</template>
          </el-table-column>
          <el-table-column fixed="right" prop="taxPoint" width="120" label="代扣税点">
            <template slot-scope="scope">{{ scope.row.taxPoint }}元</template>
          </el-table-column>
          <el-table-column fixed="right" prop="realMoney" width="120" label="实际支付">
            <template slot-scope="scope">{{ scope.row.realMoney }}元</template>
          </el-table-column>
          <el-table-column fixed="right" prop="billMoney" width="150" label="本次结佣">
            <template slot-scope="scope">
              <el-input
                placeholder="请输入开票金额"
                style="width:100%"
                v-model.number="scope.row.billMoney"
              ></el-input>
            </template>
          </el-table-column>
          <el-table-column fixed="right" align="center" label="操作" width="100">
            <template slot-scope="scope">
              <i style="cursor: pointer;" @click="ClickDelet(scope.row)" class="el-icon-delete"></i>
            </template>
          </el-table-column>
        </el-table>
      </template>
      <!-- 认购单列表 -->
      <!-- 开票总金额 -->
      <el-card class="dialog-footer">
        <!-- <el-button @click="resetForm()">取 消</el-button> -->
        <div>
          开票总金额 :
          <span style="color:red">￥{{totolFn}}</span>
        </div>
        <el-button class="saveColor" type="primary" @click="onSubmit">保存</el-button>
      </el-card>
      <!-- 开票总金额 -->
    </el-dialog>

    <!-- 选择账户 -->
    <Selectaccount ref="Selectaccount" @chooseData="chooseData" :dialogVisible="Selectaccount" />
    <!-- 选择账户 -->
    <!-- 认购单选择 -->
    <Subscription
      @handleSelectionChange="handleSelectionChange"
      ref="Subscription"
      :dialogVisible="SubscriptionData"
    />
    <!-- 认购单选择 -->
  </div>
</template>
<script>
import {
  addDeptInvoice,
  deptList,
  bankAccountList,
  commitDeptInvoice
} from "@/api/business/management";
import Selectaccount from "./Selectaccount";
import Subscription from "./Subscription";
export default {
  components: {
    Selectaccount,
    Subscription
  },
  data() {
    return {
      formInline: {
        //添加发票传参
        deptId: "", //门店ID
        baId: "", //门店银行账号ID
        scale: 2, //代扣税点 1：不扣税 2：扣3% 3：扣6%
        type: 2, //结佣类型 1：垫佣 2：正常结佣
        isBill: true, //是否开票 1：是 0：否
        title: "", //发票抬头 1：云经纪 2：创优 3：优创动力
        invoiceType: "", //发票类型 1专票 2普票
        remark: "", //备注
        details: [] //成交单ID、结佣金额、代扣税点、实际支付
      },
      labelPosition: "center",
      depiQuery: {
        //门店传参
        departmentName: "",
        pageSize: 10,
        pageNo: 1
      },
      state: "", //门店虚值
      name: null, //账户虚值
      Selectaccount: {
        //选择账户
        trunoff: false,
        deptId: null,
        data: null //门店的银行账户
      },
      rules: {
        deptId: [{ required: true, message: "银行卡号", trigger: "blur" }],
        baId: [
          { required: true, message: "银行卡的户主姓名", trigger: "blur" }
        ],
        scale: [{ required: true, message: "请选择银行", trigger: "blur" }],
        type: [{ required: true, message: "请选择银行", trigger: "blur" }],
        isBill: [{ required: true, message: "请选择银行", trigger: "blur" }],
        invoiceType: [
          { required: true, message: "请选择银行", trigger: "blur" }
        ],
        title: [{ required: true, message: "请选择银行", trigger: "blur" }],
        name: [{ required: true, message: "请选择银行", trigger: "blur" }]
      },
      SubscriptionData: {
        //认购单
        trunoff: false
      },
      SubscriptionArr: null, //认购单数据
      types: {
        1: 0,
        2: 0.03,
        3: 0.06,
        4: 0.05
      },
      selecount: null //银行账户信息
    };
  },
  methods: {
    handleClose(done) {
      done();
      this.restFrom()
    },
    //提交
    onSubmit() {
      this.$refs.elform.validate(valid => {
        if (valid) {
          let data = JSON.parse(JSON.stringify(this.formInline));
          data.isBill = data.isBill ? 1 : 0;
          if (
            this.SubscriptionArr == null ||
            this.SubscriptionArr.length == 0
          ) {
            return this.$message.error("请选择订购单");
          }
          let details = this.SubscriptionArr.map(item => {
            let obj = {};
            obj.ticketId = item.ticketId;
            obj.billMoney = item.billMoney;
            obj.taxPoint = item.taxPoint;
            obj.realMoney = item.realMoney;
            return obj;
          });
          data.details = details;
          addDeptInvoice(data)
            .then(res => {
              if (res.data.code === "000") {
                this.$confirm("保存成功, 是否立即提交?", "提示", {
                  confirmButtonText: "是",
                  cancelButtonText: "否",
                  type: "warning"
                })
                  .then(() => {
                    commitDeptInvoice({
                      invoiceId: res.data.bizData.invoiceId
                    }).then(res => {
                      if (res.data.code === "000") {
                        this.$message({
                          type: "success",
                          message: "提交成功!"
                        });
                        this.restFrom()
                        this.$parent.deptInvoiceList()
                      }
                    });
                  })
                  .catch(() => {
                    this.$message({
                      type: "success",
                      message: "保存成功"
                    });
                    this.restFrom()
                    this.$parent.deptInvoiceList()
                  });
              }
            })
            .catch(err => {
              console.log(err);
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //门店列表
    deptIdList(cb) {
      deptList(this.depiQuery).then(res => {
        if (res.data.code === "000") {
          let data = res.data.bizData.items.map(item => {
            item.value = item.departmentName;
            return item;
          });
          cb(data);
        }
      });
    },
    querySearch(queryString, cb) {
      this.depiQuery.departmentName = queryString;
      this.deptIdList(cb);
      // 调用 callback 返回建议列表的数据
    },
    handleSelect(val) {
      this.formInline.deptId = val.departmentId;
      this.Selectaccount.deptId = val.departmentId;
      this.bankAccountList();
    },
    //门店列表
    // 选择账户
    baIdFn() {
      if (this.formInline.deptId == "") {
        this.formInline.baId = "";
        return this.$message.error("请选择门店");
      }
      this.Selectaccount.trunoff = true;
    },
    //获取门店的银行账户
    bankAccountList() {
      bankAccountList({ deptId: this.formInline.deptId }).then(res => {
        if (res.data.code === "000") {
          this.Selectaccount.data = res.data.bizData.items;
          this.$refs.Selectaccount.chooseData = this.selecount;
          //  console.log(this.$refs.Selectaccount.chooseData)
        }
      });
    },
    //添加认购单
    addFn() {
      this.$nextTick(() => {
        if (
          this.formInline.deptId == "" ||
          this.formInline.deptId == undefined
        ) {
          return this.$message.error("请选择门店");
        }
        this.$refs.Subscription.multipleSelection = this.SubscriptionArr;
        this.$refs.Subscription.selDeptTicketList(
          this.formInline.deptId,
          this.formInline.type
        );
        this.SubscriptionData.trunoff = true;
      });
    },
    //认购单数据传递
    handleSelectionChange(val) {
      this.SubscriptionArr = JSON.parse(JSON.stringify(val)).map(item => {
        item.taxPoint = 0;
        item.realMoney = 0;
        item.billMoney = "";
        return item;
      });
    },
    //银行账户选择
    chooseData(val) {
      this.formInline.baId = val;
      let obj = this.Selectaccount.data.find(item => {
        return item.baId == val;
      });
      console.log(obj);
      this.name = obj.accountName;
    },
    //删除
    ClickDelet(val) {
      this.SubscriptionArr.forEach((item, index) => {
        if (item.ticketId === val.ticketId) {
          this.SubscriptionArr.splice(index, 1);
        }
      });
    },
    //重置
    restFrom(){
      this.$refs.elform.resetFields();
      this.state=''
      this.name=''
      this.selecount=null
      this.dialogVisible.trunoff=false
    }
   
  },
  props: {
    dialogVisible: {
      type: Object
    }
  },
  computed: {
    isBillFn() {
      return this.formInline.isBill;
    },
    tableDataFn() {
      if (this.SubscriptionArr == null || this.SubscriptionArr.length == 0) {
        return false;
      }
      return this.SubscriptionArr;
    },
    totolFn() {
      let totol = 0;
      if (this.SubscriptionArr == null || this.SubscriptionArr.length == 0) {
        return totol;
      }
      this.SubscriptionArr.forEach(item => {
        item.canCommission < item.billMoney
          ? (item.billMoney = item.canCommission)
          : "";
        item.realMoney =
          item.billMoney == "" || item.taxPoint == "" || item.billMoney == 0
            ? 0
            : Number(item.billMoney) - Number(item.taxPoint);
        totol += Number(item.billMoney);
        item.taxPoint =
          item.billMoney == ""
            ? 0
            : Number(
                (
                  Number(item.billMoney) * this.types[this.formInline.scale]
                ).toFixed(2)
              );
      });
      return totol;
    }
  }
};
</script>
<style lang="scss" scoped>
.addment {
  box-sizing: border-box;
  .dialog-footer {
    margin-top: 10px;
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    /deep/.el-card__body {
      display: flex;
      align-items: center;
      padding: 0;
      font-size: 18px;
      .saveColor {
        border-radius: 0;
        width: 160px;
        margin-left: 20px;
      }
    }
  }
  .el-form-item__content {
    width: 80%;
    box-sizing: border-box;
  }
  .addIcon {
    font-size: 22px;
    margin-top: 8px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    padding: 10px 0;
    font-family: SimSun;
  }
  /deep/.el-dialog {
    background-color: rgb(245, 241, 241);
    .el-dialog__body {
      padding-top: 0;
      padding-bottom: 1px;
    }
  }
}
</style>
