<template>
  <div class="upload">
    <!-- 上传开票信息 -->
    <el-card class="title">
      <img src="../../../assets/icons/left.png" />
      <span @click="$emit('InvoiceOffFN')">返回门店详情</span>
      <b>上传收票信息</b>
    </el-card>
    <div class="scoll_context">
      <el-card>
        <div slot="header" class="clearfix">
          <span>{{InvoiceDatas.deptName}}&nbsp;&nbsp;{{InvoiceDatas.addDate}}</span>
        </div>
        <el-row>
          <el-col :span="8">
            <div style="min-width:100px;text-align: right;">是否开票:</div>
            <el-switch
              class="rights_text"
              :active-color="'#0971cc'"
              :value="ideaTop.isBill==1?true:false"
              disabled
            ></el-switch>
          </el-col>
          <el-col :span="8">
            <div style="min-width:100px;text-align:right;">结佣金额:</div>
            <strong class="rights_text" style="color:red">{{ideaTop.money}}￥</strong>
          </el-col>
          <el-col :span="8">
            <div style="min-width:100px;text-align: right;">账户:</div>
            <strong class="rights_text">{{ideaTop.accName}}</strong>
            <img
              style="margin-left:10px;min-width:20px; cursor: pointer;"
              @click="AccountOff.trunoff=true"
              src="../../../assets/icons/alert.png"
            />
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="8">
            <div style="min-width:100px;text-align: right;">发票类型:</div>
            <strong class="rights_text">{{ideaTop.invoiceType==1?'专票':'普票'}}</strong>
          </el-col>
          <el-col :span="8">
            <div style="min-width:100px;text-align: right;">发票抬头:</div>
            <strong class="rights_text">{{ideaTop.title}}</strong>
            <img
              style="margin-left:10px;min-width:20px;max-height: 20px; cursor: pointer;"
              @click="infoOff.trunoff=true"
              src="../../../assets/icons/alert.png"
            />
          </el-col>
          <el-col :span="8">
            <div style="min-width:100px;text-align: right;">代扣税点:</div>
            <strong
              class="rights_text"
            >{{ideaTop.scaleId==1?'不扣税': ideaTop.scaleId==2?'扣3%':ideaTop.scaleId==3?'扣6%':ideaTop.scaleId== 4?'扣5%':''}}</strong>
          </el-col>
        </el-row>

        <el-form :rules="rules" :model="formInline">
          <!-- <el-form-item prop="deliverUid" label="派送人:">
            <el-autocomplete
              class="inline-input"
              style="width:90%;"
              v-model="values"
              :fetch-suggestions="standUidSearch"
              placeholder="请输入对佣人"
              @select="handleSelectstandUid"
              @keydown.native="keydownFnss"
            ></el-autocomplete>
          </el-form-item> -->
          <el-form-item prop label="开票凭证:">
            <upPictrue />
          </el-form-item>
          <el-form-item prop="remark" label="备注:">
            <el-input
              style="width:90%;"
              type="textarea"
              v-model="formInline.remark"
              placeholder="请输入备注"
            ></el-input>
          </el-form-item>
        </el-form>
        <div class="miss_down">
          <div @click="submit">
            <img src="../../../assets/icons/审核icon.png" />
            提交
          </div>
        </div>
      </el-card>
    </div>

    <!-- 发票抬头弹窗 -->
    <oJournal title="发票抬头" :dialogVisible="infoOff" />
    <!-- 发票抬头弹窗 -->
    <!-- 账户弹窗 -->
    <oJournal title="账户" :dialogVisible="AccountOff" />
    <!-- 账户弹窗 -->
  </div>
</template>
<script>
import {openDeptInvoice } from "@/api/business/management";
import oJournal from "components/common/oJournal";
import upPictrue from "../../common/upPictrue";
export default {
  components: {
    oJournal,
    upPictrue
  },
  data() {
    return {
      urls: [],
      values: "",
      formInline: {
        remark: "",
        // deliverUid: "",
        picUrl: [],
        invoiceId: ""
      },
      rules: {
        deliverUid: [
          { required: true, message: "请填写活动形式", trigger: "blur" }
        ],
        remark: [{ required: true, message: "请填写活动形式", trigger: "blur" }]
      },
      infoOff: {
        //发票抬头弹窗
        trunoff: false,
        data: {
          kfsName: "",
          dutyNo: "",
          bankName: "",
          mobile: "",
          address: "",
          account: "",
          name: ""
        }
      },
      AccountOff: {
        //账户弹窗
        trunoff: false,
        data: {
          bankName: "",
          accName: "",
          accountId: ""
        }
      },
      selectstandUid: {
        //对佣人搜索条件
        pageSize: 40,
        pageNo: 1,
        key: "",
        deptId: ""
      },
      selectItemstandUid: null
    };
  },
  props: {
    InvoiceDatas: {
      type: Object
    },
    ideaTop: {
      type: Object
    }
  },
  methods: {
    submit() {
      this.formInline.invoiceId = this.$store.getters.invoiceId;
      this.formInline.picUrl = this.urls;
      openDeptInvoice(this.formInline).then(res => {
        if(res.data.code==='000'){
            this.$message.success('已收票')
            this.$parent.$parent.onSubmit()
           this.$parent.$parent.handleClick()
           this.$emit('InvoiceOffFN')
        }
      });
    },
    upLoad() {
      let imgs = document.getElementById("img-upload");
      imgs.click();
      var that = this;
      imgs.onchange = function(e) {
        //创建读取文件的对象
        const reader = new FileReader();
        //使用该对象读取file文件
        reader.readAsDataURL(e.target.files[0]);
        //读取文件成功后执行的方法函数
        reader.onload = function(e) {
          //读取成功后返回的一个参数e，整个的一个进度事件
          //选择所要显示图片的img，要赋值给img的src就是e中target下result里面
          //的base64编码格式的地址
          console.log(e.target.result);
          that.urls.push(e.target.result);
        };
      };
    },
    handlerData() {},
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      console.log(file);
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    // 搜索条件派送人
    async standUidSearch(queryString, cb) {
      this.selectstandUid.key = queryString;
      await this.requestSearchTwo(cb);
    },
    handleSelectstandUid(val) {
      console.log(val);

      this.formInline.deliverUid = val.uid;
      this.selectItemstandUid = val;
    },
    keydownFnss() {
      if (this.values.length === 0) {
        this.formInline.standUid = "";
        this.selectstandUid.key = "";
      }
    },
    requestSearchTwo(cb) {
      this.selectstandUid.deptId = this.$store.getters.userinfo.departmentId;
      userList(this.selectstandUid).then(res => {
        // console.log(res)
        if (res.data.code === "000") {
          if (res.data.bizData.items.length === 0) {
            return cb([{}]);
          }
          this.standUidArr = res.data.bizData.items.map(item => {
            item.value = item.name;
            return item;
          });
          var restaurants = this.standUidArr;
          return cb(restaurants);
        }
      });
    },
    // 搜索条件派送人

    //数据操作渲染
    handDataBar() {
      let datas = this.AccountOff.data;
      let details = this.$store.getters.InvoiceDetail;
      let companyList = this.$store.getters.companyList;
      let data = companyList.find(item => {
        return item.companyId === details.titleId;
      });
      this.infoOff.data = data;
      for (let key in this.ideaTop) {
        if (key === "status") {
          this.ideaTop[key] = details[key].toString();
          continue;
        }
        this.ideaTop[key] = details[key];
      }
      for (let key in datas) {
        datas[key] = details[key];
      }
    }
  },
  mounted() {
    this.handDataBar();
  }
};
</script>
<style lang="scss">
@import "@/css/common.scss";
.upload {
  margin-top: 20px;

  .scoll_context {
    height: calc(100vh - 200px);
    overflow-y: auto;
    margin-top: 20px;
    .kfsuser {
      display: flex;
      padding: 0 10px;
      justify-content: space-between;
      height: 40px;
      align-items: center;
      border: 1px solid rgb(110, 188, 224);
      border-radius: 6px;
      margin-bottom: 20px;
      background-color: rgb(217, 232, 240);
    }
    .miss_down {
      display: flex;
      justify-content: center;
      margin-top: 70px;
      margin-bottom: 50px;

      div {
        font-size: 26px;
        padding: 6px 16px;
        background-color: #0b8d7e;
        width: 140px;
        color: #fff;
        border-radius: 6px;
        text-align: center;
        cursor: pointer;
        img {
          vertical-align: middle;
        }
      }
    }
    #img-upload {
      display: none;
    }
    .yulan {
      display: flex;
    }
    .upimg {
      display: inline-block;
      width: 120px;
      height: 130px;
    }
  }
}
</style>