
<template>
  <!-- 结佣-------认购单选择 -->
  <el-dialog
    title="结佣-选择认购单"
    :visible.sync="dialogVisible.trunoff"
    width="80%"
    :before-close="handleClose"
    top="20px"
  >
    <!-- 搜索 -->
    <div class="top_subscription">
      <!-- <div style="width:120px;">客户或电话号码:</div> -->
      <el-input v-model="queryFrom.keyName" style="width:230px;" placeholder="请输入客户或电话号码搜索"></el-input>
      <el-button @click="seachFn">搜索</el-button>
    </div>
    <!-- 搜索 -->
    <!-- 表格 -->
    <el-table
      ref="multipleTable"
      :data="tableDataFn"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange"
      height="400px"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="customName" label="姓名" width="120"></el-table-column>
      <el-table-column prop="preMobile" label="电话号码">
          <template slot-scope="scope">{{ scope.row.preMobile+scope.row.midMobile+scope.row.sufMobile }}</template>
      </el-table-column>
      <el-table-column prop="projectName" label="楼盘名字"></el-table-column>
      <el-table-column prop="roomNo" label="房号"></el-table-column>
      <el-table-column label="认购日期" width="220">
        <template slot-scope="scope">{{ scope.row.tradeTime }}</template>
      </el-table-column>
      <el-table-column prop="sumMoney" label="认购金额">
        <template slot-scope="scope">{{ scope.row.sumMoney }}元</template>
      </el-table-column>
      <el-table-column prop="sumComission" label="总佣金">
        <template slot-scope="scope">{{ scope.row.sumComission }}元</template>
      </el-table-column>
      <el-table-column prop="haspay" label="已结佣">
        <template slot-scope="scope">{{ scope.row.haspay }}元</template>
      </el-table-column>
      <el-table-column prop="unpay" label="未结佣">
        <template slot-scope="scope">{{ scope.row.unpay }}元</template>
      </el-table-column>
      <el-table-column prop="unbilled" label="未开票">
        <template slot-scope="scope">{{ scope.row.unbilled }}元</template>
      </el-table-column>
      <el-table-column prop="canCommission" label="可结金额">
        <template slot-scope="scope">{{ scope.row.canCommission }}元</template>
      </el-table-column>
      <!-- <el-table-column prop="address" label="地址"></el-table-column>
      <el-table-column prop="address" label="地址"></el-table-column>
      <el-table-column prop="address" label="地址"></el-table-column>
      <el-table-column prop="address" label="地址"></el-table-column>-->
    </el-table>
    <Pagination
      @handleSizeChange="handleSizeChange"
      @handleCurrentChange="handleCurrentChange"
      :paginationData="paginationData"
    />

    <!-- 表格 -->
    <!-- <div style="margin-top: 20px">
      <el-button @click="toggleSelection([tableData[1], tableData[2]])">切换第二、第三行的选中状态</el-button>
      <el-button @click="toggleSelection()">取消选择</el-button>
    </div>-->
    <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible.trunoff = false">取 消</el-button>
      <el-button type="primary" @click="sure">确 定</el-button>
    </span>
  </el-dialog>
</template>
<script>
import { selDeptTicketList } from "@/api/business/management";
import { dateTime } from "@/utils/common";
export default {
  props: {
    dialogVisible: {
      type: Object
    }
  },
  data() {
    return {
      tableData: [
        {
          ticketId: 2, //成交单ID
          customName: "客户姓名", //客户姓名
          preMobile: "", //客户手机号
          midMobile: "", //客户手机号
          sufMobile: "", //客户手机号
          projectId: 23, //楼盘ID
          projectName: "", //楼盘名字
          departmentId: 123, // 门店ID
          departmentName: "", //门店名字
          tradeTime: "", //认购时间
          roomNo: "房号",
          area: "110",
          sumMoney: "1890000", //"总金额
          sumComission: "60000", //总佣金
          unpay: "30000", //未结佣金额
          unbilled: "60000", //未开票金额
          haspay: "20000", //已结佣金额
          canCommission: "10000" //门店佣金可结
        }
      ],
      multipleSelection: [],
      paginationData: {
        //分页
        pageSize: 10,
        currentPage: 1,
        total:100
      },
      queryFrom: {
        deptId: "",
        keyName: "",
        type: "",
        pageNo: 1,
        pageSize: 10
      },
      departmentId: []
    };
  },
  methods: {
    handleClose(done) {
      done();
    },
    toggleSelection(rows) {
      this.$nextTick(() => {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      });
    },
    // 多选事件
    handleSelectionChange(val) {
   
      this.multipleSelection = val;
   
    },
    selDeptTicketList(val, vals) {
      this.queryFrom.deptId = val;
      this.queryFrom.type = vals;
      // this.queryFrom.deptId=this.$store.getters.userinfo.departmentId
      // this.queryFrom.type=val
      selDeptTicketList(this.queryFrom).then(res => {
        if (res.data.code === "000") {
          this.tableData = res.data.bizData.items.map(item => {
            item.tradeTime = dateTime(item.tradeTime);
            return item;
          });
          this.checkBox()
          let arr = [];
          this.tableData.forEach((item, index) => {
            if (
              this.departmentId.length != 0 &&
              this.departmentId.indexOf(item.ticketId) != -1
            ) {
              arr.push(this.tableData[index]);
            }
          });
          this.toggleSelection(arr);
        }
      });
    },
    handleSizeChange(val) {
      this.queryFrom.pageSize = val;
      this.seachFn();
    },
    handleCurrentChange(val) {
      this.queryFrom.pageNo = val;
      this.seachFn();
    },
    // 搜索
    seachFn() {
      selDeptTicketList(this.queryFrom).then(res => {
        if (res.data.code === "000") {
          this.tableData = res.data.bizData.items.map(item => {
            item.tradeTime = dateTime(item.tradeTime);
            return item;
          });

        }
      });
    },
    // 确定
    sure() {
      this.checkBox()
      this.$emit("handleSelectionChange", this.multipleSelection);
      this.dialogVisible.trunoff = false;
    },
    //选中cke
    checkBox(){
        let departmentId = [];
        if(this.multipleSelection.length!=0){this.multipleSelection.forEach(item => {
           departmentId.push(item.ticketId);
          });
        }
      this.departmentId = departmentId;
    }
  },
  computed: {
    tableDataFn() {
      this.paginationData.total=this.tableData.length
      return this.tableData;
    }
  },
  updated() {
    // console.log('opioi')
  }
};
</script>
<style lang="scss">
.top_subscription {
  display: flex;
  align-items: center;
  .el-button {
    margin-left: 20px;
  }
  margin-bottom: 10px;
}
</style>