<template>
<!-- 确认结佣弹窗 -->
  <el-dialog
    :close-on-click-modal="false"
    :show-close="false"
    :visible.sync="dialogVisible.trunoff"
     width="30%"
  >
    <div class="title_examine">
      确认结佣
      <img @click="colseFn()" src="../../../assets/icons/esc.png" />
    </div>
    <el-form ref="form" class="examine" :model="form">
      <el-form-item>
        <el-radio-group v-model="form.checkStatus">
          <el-radio label="确认结佣" :value="1"></el-radio>
          <el-radio label="拒绝结佣" :value="2"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="原因:">
        <el-input type="textarea" placeholder="请输入你的建议" v-model="form.remark"></el-input>
      </el-form-item>
      <el-form-item>
      <el-button style="background-color: #0b8d7e;color:#fff;"  @click="onSubmit">提交</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>
<script>
import {commDeptInvoice} from '@/api/business/management'
export default {
  props: {
    dialogVisible: {
      type: Object
    },
    type:{
      type:String
    }
  },
  data() {
    return {
      form: {
        checkStatus: "",
        remark:'',
        invoiceId:''
      }
    }
  },
  methods: {
   onSubmit() {
     this.form.invoiceId=this.$store.getters.invoiceId
      this.form.checkStatus=this.form.checkStatus==='确认结佣'?1:2
      commDeptInvoice(this.form).then(res=>{
             if(res.data.code==='000'){
                 this.$message.success('结佣完成')
                 this.colseFn()
             }
      })
      },
     colseFn(){
        this.$parent.$parent.onSubmit()
        this.$parent.$parent.handleClick()
        this.dialogVisible.trunoff=false
    }
        
    },
  
  }
</script>
<style lang="scss" scoped>
.examine{
 .el-form-item:nth-child(1),.el-form-item:nth-child(3){
       margin-bottom: 0px;
       text-align: center;
       
  }
}

/deep/.el-dialog {
  position: relative;
  border-radius: 6px;
  .el-dialog__header {
    padding: 0;
  }
  
  .title_examine {
    text-align: center;
    width: 100%;
    font-size: 24px;
    color: #000;
    img {
      position: absolute;
      top: 0;
      right: 10px;
    }
  }
  .el-dialog__body {
    padding: 10px 30px;
  }
}
</style>