<template>
  <div>
    <!-- 银行选择 -->
    <el-dialog
      class="selectunt"
      :visible.sync="dialogVisible.trunoff"
      width="40%"
      :before-close="handleClose"
    >
      <div class="title_selectunt">选择账户</div>
      <div class="item_selectunt">
        <template v-if="dialogVisible.data">
          <el-radio  v-model="chooseData" :label="item.baId" v-for="(item,index) in dialogVisible.data" :key="index">
            <div class="type">{{item.bankName}}</div>
            <div class="name">{{item.accountName}}</div>
            <div class="number">{{item.account}}</div>
          </el-radio>
        </template>
      </div>
      <i @click="AddFn" class="el-icon-plus addIcon">添加新的银行账户</i>
    </el-dialog>
    <!-- 门店添加账户 -->
        <el-dialog
          class="selectunt_child"
          :visible="child"
          width="40%"
          :before-close="handleClosechild"
        >
          <div class="child_title">门店添加账户</div>
          <el-form ref="formName" :rules="rules" :model="addBankAccountData">
            <el-form-item prop="bankId" label-width="90px" label="所属银行:">
             <el-select style="width:100%;" v-model="addBankAccountData.bankId" placeholder="活动区域">
               <el-option
                 v-for="(item) in $store.getters.bankList"
                 :key="item.bankId"
                 :label="item.bankName"
                 :value="item.bankId"
               ></el-option>
             </el-select>
               </el-form-item>
               <el-form-item prop="accountName" label-width="90px" label="户名:">
             <el-input
               style="width:100%;"
               v-model="addBankAccountData.accountName"
               placeholder="请输入户名"
             ></el-input>
           </el-form-item>
           <el-form-item prop="account" label-width="90px" label="银行账号:">
             <el-input style="width:100%;" v-model="addBankAccountData.account" placeholder="请输入银行账号"></el-input>
           </el-form-item>
          </el-form>
          <el-button @click="AddChild" class="btn">确定添加</el-button>
        </el-dialog>
    <!-- 门店添加账户 -->
  </div>
</template>
<script>
import {addBankAccount} from "@/api/business/management";
export default {
  props: {
    dialogVisible: {
      type: Object
    }
  },
  data() {
    return {
      child: false, //门店添加账户
      deptId: null, //门店ID
      addBankAccountData: {
        //添加银行传参
        deptId: "", //门店ID
        account: "", //银行卡号
        accountName: "", //银行卡的户主姓名
        bankId: "" //对应着银行
      },
      rules: {
        account: [{ required: true, message: "银行卡号", trigger: "blur" }],
        accountName: [
          { required: true, message: "银行卡的户主姓名", trigger: "blur" }
        ],
        bankId: [{ required: true, message: "请选择银行", trigger: "blur" }]
      },
      chooseData:null //选择的银行账户
    };
  },
  methods: {
    //   关闭
    handleClose(done) {
     this.dialogVisible.trunoff=false
    },
    handleClosechild(done) {
      this.child = false;
    },
    //   关闭
    //添加新的银行账户
    AddFn() {
      this.child = true;
    },

    //确认添加银行
    AddChild() {
      this.addBankAccountData.deptId = this.dialogVisible.deptId;
      this.$refs.formName.validate(valid => {
        if (valid) {
          addBankAccount(this.addBankAccountData).then(res => {
            if(res.data.code==='000'){
              this.$parent.bankAccountList()
              this.$message.success('添加成功')
              this.resetForm('formName')
              this.child=false
            }
          });
        }
      });
    },
    // 重置
     resetForm(formName) {
        this.$refs[formName].resetFields();
    }
  },
  watch:{
    chooseData:{
      handler(val,old){
        this.$emit('chooseData',val)
        this.dialogVisible.trunoff=false
      }
    }
  }
};
</script>
<style lang="scss" scope>
.selectunt /deep/.el-dialog,
.selectunt_child /deep/.el-dialog {
  background-color: #fff !important;
  text-align: center;
  .item_selectunt{
    max-height: 300px;
    overflow-y: auto;
  }
  .item_selectunt::-webkit-scrollbar{
    width: 0;
  }
  
  .btn{
    margin-bottom: 20px;
  }

  .type {
    font-size: 22px;
    font-weight: 500;
    padding-bottom: 4px;
    color: #000;
  }
  .name {
    color: rgb(190, 186, 186);
    font-size: 14px;
    margin-bottom: 20px;
  }
  .number {
    font-size: 18px;
    color: #000;
  }
 
}
.selectunt_child .el-dialog__body{
  text-align: center;
  .child_title{
    font-size: 20px;
    margin-bottom: 10px;
    width: 100%;
    text-align: center;
  }
}

.selectunt{
  .title_selectunt {
    display: flex;
    justify-content: center;
    font-size: 20px;
    padding-bottom: 10px;
  }
 
   .addIcon {
    font-size: 22px;
    color: rgb(95, 72, 226);
    padding: 20px 0;
    cursor: pointer;
    display: flex;
    justify-content: center;
  }
 .el-radio-group {
  width: 100%;
}
.el-radio {
  display: flex;
  align-items: center;
  margin-right: 0px;
  background-color: rgb(233, 236, 236);
  margin-bottom: 20px;
  padding: 8px 10px;
  border-radius: 6px;
}
}
</style>