<template>
  <div class="commission">
    <template v-if="!idetails">
      <!--title 结佣管理 -->
      <!-- 查询 -->
      <el-card>
        <el-form :inline="true" :model="queryfrom" class="me_from demo-form-inline">
          <el-form-item>
            <el-autocomplete
              class="inline-input"
              v-model="value"
              style="width:190px;"
              :fetch-suggestions="querySearch"
              placeholder="门店"
              @select="handleSelect"
              @keydown.native="keydownFn"
            ></el-autocomplete>
          </el-form-item>
          <el-form-item>
            <el-select style="width:190px;" v-model="queryfrom.status" placeholder="请选择状态">
              <el-option label="全部" :value="0"></el-option>
              <el-option label="已保存" :value="1"></el-option>
              <el-option label="已提交" :value="2"></el-option>
              <el-option label="已审核" :value="3"></el-option>
              <el-option label="已收票" :value="5"></el-option>
              <el-option label="已结佣" :value="6"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-date-picker
              v-model="value1"
              type="daterange"
              style="width:290px;"
              range-separator="至"
              start-placeholder="选择开始时间"
              end-placeholder="选择结束时间"
            ></el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-input style="width:190px;" v-model="queryfrom.keyWord" placeholder="申请人"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" style="background:#0b8d7e;border:none;" @click="onSubmit">搜索</el-button>
            <el-button type="primary" style="background:#f1420ef7;border:none;" @click="AddPro">添加发票</el-button>
          </el-form-item>
        </el-form>
      </el-card>
      <!-- 查询 -->
      <!-- 表格 -->
      <el-card style="margin-top:20px;">
        <header style="font-weight: 800;padding-bottom: 8px;color: #000;">结佣列表</header>
        <el-table
          ref="table"
          height="410"
          :cell-style="cellStyle"
          :header-cell-style="{background:'#edfaf8',color:'#333333'}"
          :row-class-name="tableRowClassName"
          :data="tableData"
          border
          style="width: 100%"
        >
          <el-table-column prop="deptName" label="门店名称" width="180"></el-table-column>
          <el-table-column prop="time" label="日期" width="180"></el-table-column>
          <el-table-column prop="type" label="结佣类型">
            <template slot-scope="scope">{{scope.row.type===1?'垫佣':scope.row.type===2?'正常结佣':''}}</template>
          </el-table-column>
          <el-table-column prop="money" label="结佣金额">
            <template slot-scope="scope">{{scope.row.money}}元</template>
          </el-table-column>
          <el-table-column prop="realMoney" label="实际支付金额">
            <template slot-scope="scope">{{scope.row.realMoney}}元</template>
          </el-table-column>
          <template v-if="this.queryfrom.status===3||this.queryfrom.status===5">
            <el-table-column prop="status" label="状态" width="100">
              <template
                slot-scope="scope"
              >{{scope.row.status===3?'审核通过':scope.row.status===4?'审核拒绝':scope.row.status===7?'拒绝开票':scope.row.status===5?'已开票':''}}</template>
            </el-table-column>
          </template>
          <el-table-column prop="addName" label="申请人姓名"></el-table-column>
          <el-table-column fixed="right" label="操作" width="100">
            <template slot-scope="scope">
              <el-button @click="handleClick(scope.row)" type="text" size="small">门店详情</el-button>
            </template>
          </el-table-column>
        </el-table>
        <Pagination
          @handleSizeChange="handleSizeChange"
          @handleCurrentChange="handleCurrentChange"
          :paginationData="paginationData"
        />
      </el-card>
      <!-- 表格 -->
    </template>
    <!-- 添加门店对佣 -->
    <Addment :dialogVisible="AddmentData"/>
    <!-- 添加门店对佣 -->
    <!-- 对佣详情 -->
    <idetails :InvoiceLogInfo="InvoiceLogInfo" @InvoiceOffFN="idetails=false" v-if="idetails" />
    <!-- 对佣详情 -->
  </div>
</template>
<script>
import {
  deptList,
  deptInvoiceList,
  deptInvoiceDetail,
  companyList,
  deptInvoiceLog,
  bankList
} from "@/api/business/management";
import { dateTime } from "@/utils/common";
import Addment from 'components/business/management/Addment'
import idetails from "components/business/management/idetails";
import myMixin from "@/mixin/index";
export default {
  mixins: [myMixin],
  components: {
    idetails,
    Addment
  },
  data() {
    return {
      selectQuery: {
        //门店传参
        departmentName: "",
        pageSize: 10,
        pageNo: 1
      },
      stores: null, //门店列表
      queryfrom: {
        //查询操作
        deptId: 0,
        status: "",
        pageNo: 1,
        pageSize: 10,
        keyWord:'',
        beginTime:'',
        endTime:''
      },
      paginationData: {
        //分页
        pageSize: 10,
        currentPage: 1,
        total: 100
      },
      tableData: [], //数据
      value: "",
      // 门店详情
      idetails: false, //详情控制弹窗页面
      InvoiceLogInfo: null, //日志
      // 门店详情
      value1: "",
      // 添加门店对佣
      AddmentData:{
          trunoff:false
      }
    };
  },

  updated() {
    if (this.$refs["table"] == undefined) return;
    this.$refs["table"].doLayout();
  },
  methods: {
    //////////////top查询事件
    onSubmit() {
      this.queryfrom.pageSize=10
      this.queryfrom.pageNo=1
      this.deptInvoiceList();
    },
    // 搜索
    handleSelect(item) {
      this.queryfrom.deptId = item.departmentId;
    },
    querySearch(queryString, cb) {
      this.selectQuery.departmentName = queryString;
      this.requestList(cb);
    },
    requestList(cb) {
      //门店列表
      deptList(this.selectQuery).then(res => {
        if (res.data.code === "000") {
          this.stores = res.data.bizData.items;
          if (this.stores.length === 0 || this.stores == null) {
            cb([{}]);
          } else {
            var resdwda = this.stores.map(item => {
              item.value = item.departmentName;
              return item;
            });
            cb(resdwda);
          }

          // 调用 callback 返回建议列表的数据
        }
      });
    },
    keydownFn() {
      if (this.value.length === 1 || this.value.length === 0) {
        this.queryfrom.deptId = 0;
      }
    },
    // 搜索
    // 查询
    deptInvoiceList() {
      deptInvoiceList(this.queryfrom).then(res => {
        if (res.data.code === "000") {
          this.tableData = res.data.bizData.items.map(item => {
            item.time = dateTime(item.time);
            return item;
          });
          this.paginationData.total=res.data.bizData.total
        }
      });
    },
    //添加发票
    AddPro(){
       this.AddmentData.trunoff=true
    },
    //////////////top查询事件
    // 门店详情
   handleClick(item) {
     if(!(item==null||item=='')){
        this.$store.dispatch("invoiceIdFn", item.invoiceId);
     }
      deptInvoiceDetail({ invoiceId: this.$store.getters.invoiceId }).then(res => {
        if (res.data.code === "000") {
          this.$store.dispatch("InvoiceDetail", res.data.bizData);
          this.idetails = true;
        }
      });
    
      deptInvoiceLog({ invoiceId: this.$store.getters.invoiceId}).then(res => {
        if (res.data.code === "000") {
          this.InvoiceLogInfo = res.data.bizData.items.map(item => {
            item.color = "#0bbd87";
            item.opTime = dateTime(item.opTime);
            return item;
          });
        }
      });
    },
    //分页大小
    handleSizeChange(val) {
      this.paginationData.pageSize = val;
      this.queryfrom.pageSize = val;
      this.deptInvoiceList();
    },
    //第几页
    handleCurrentChange(val) {
      this.paginationData.currentPage = val;
      this.queryfrom.pageNo = val;
      this.deptInvoiceList();
    }
  },
  mounted(){
    this.deptInvoiceList()
    bankList().then(res=>{
      if(res.data.code==='000'){
        this.$store.dispatch('bankList',res.data.bizData.items)
      }
    })
    // companyList({}).then(res => {
    //     if (res.data.code === "000") {
    //       this.$store.dispatch("companyList", res.data.bizData.items);
    //     }
    //   });
  },
   watch:{
    value1:{
      handler(){
        if(this.value1==null){
           this.queryfrom.beginTime=''
           this.queryfrom.endTime=''
           return true;
        }
        this.queryfrom.beginTime=dateTime(this.value1[0])
        this.queryfrom.endTime=dateTime(this.value1[1])
      }
    }
  }
};
</script>
<style lang="scss">
.commission {
  .me_from {
    .el-form-item {
      margin-bottom: 0;
    }
  }
}
</style>